// Parameters
// $type => "secondary", ""
//

@mixin ama-button ($size:"", $style:"") {
  @include ama-button-base;

  @if($style == "secondary") {
    background-color: $white;
    border-color: $purple;
    color: $purple;

    &:hover {
      background-color: $purple;
      color: $white;
    }
  }

  @else if($style == "tertiary") {
    background-color: $white;
    border-color: $purple;
    color: $purple;

    &:hover {
      background-color: $hoverPurple;
      color: $white;
    }
  }
  @else if($style == "complementary") {
    background-color: $complementary;
    border-color: $complementary;
    color: $black;

    &:hover {
      background-color: $hoverComplementary;
      border-color: $hoverComplementary;
      color: $gray-64;
    }
  }

  @else if($style == "complementary-secondary") {
    background-color: $white;
    border-color: $complementary;
    color: $black;

    &:hover {
      background-color: $hoverComplementary;
      border-color: $complementary;
      color: $gray-64;
    }
  }
  @else if($style == "subscribe") {
    background-color: $orange-medium !important;
    color: $black !important;
    padding-bottom: 12.5px;
    right: 2px;

    &:hover {
      border-color: transparent !important;
      background-color: $orange-light !important;
      color: $gray-64 !important;
    }
  }
  @else if($style == "news-subscribe") {
    background-color: $white;
    color: $purple;
    padding: 7px !important;
    height: 36px;
    font-size: 14px !important;
    font-weight: 600;
    border: 1px solid $purple;
    text-transform: uppercase;
    position: static;
  }

  //outline
  @else if($style == "reset") {
    background-color: transparent;
    border-color: transparent;
    color: $purple;

    &:hover {
      background-color: transparent;
      border-color: transparent;
      color: $hoverPurple;
      text-decoration: underline;
    }
  }
  @else if($style == "link") {
    background-color: transparent;
    border-color: transparent;
    color: $blue;

    &:hover {
      background-color: transparent;
      border-color: transparent;
      color: $hoverBlue;
      text-decoration: underline;
    }
  }
  // homepage
  @else if($style == "homepage") {
    background-color: $homepagePurple;
    border-color: transparent;
    color: $white;
    text-transform: uppercase;

    &:hover {
      background-color: $hoverPurple;
      border-color: transparent;
      color: $white;
      text-decoration: none;
    }
  }
  // homepage secondary
  @else if($style == "homepage-secondary") {
    background-color: $white;
    border-color: transparent;
    color: $purple;
    text-transform: uppercase;

    &:hover {
      background-color: $white;
      border-color: transparent;
      color: $hoverPurple;
      text-decoration: none;
    }
  }
  @else if($style == "promo") {
    background-color: $orange;
    border-color: transparent;
    color: $black;

    &:hover {
      background-color: $hoverOrange;
      border-color: transparent;
      color: $black;
      text-decoration: none;
    }
  }
  // pill
  @else if($style == "pill") {
    font-weight: normal;
    border-radius: 25px;
    padding: 0 $gutter;
  }
  @else if($style == "pill-secondary") {
    font-weight: normal;
    border-radius: 25px;
    padding: 0 $gutter;
    color: $purple;
    background-color: $white;

    &:hover {
      // Remove border.
      border-color: transparent;
      background-color: #dbc8e0;
      color: $purple;
    }
  }
  // pill-claim
  @else if($style == "pill-claim") {
    font-weight: normal;
    border-radius: 25px;
    padding: 0 $gutter;
    color: $purple;
    background-color: #e4d3e7;
    border: none;

    &:hover {
      background-color: $purple;
      color:$white;
    }
  }
  // icon
  @else if($style == "icon") {
    font-weight: normal;
    border-radius: 25px;
    padding: 0 $gutter;
    color: $purple;
    background-color: transparent;

    &:hover {
      background-color: #dbc8e0;
      color: $purple;
    }
  }
  @else {
    background-color: $purple;
    border-color: transparent;
    color: $white;

    &:hover {
      background-color: $hoverPurple;
    }
  }

  @if($size == "small" and $style == "secondary") {
    @include type($myriad-pro, $font-weight-regular);
    @include font-size($button-font-sizes--menu);
    padding: calc($gutter/6) calc($gutter/2);
    background-color: $white;
    border-color: $purple;
    color: $purple;

    &:hover {
      background-color: $purple;
      color: $white;
    }
  }
  @else if($size == "small") {
    @include gutter-all($padding-all-half...);
  }
  @else if ($size == "block") {
    display: block;
    width: 100%;
  }
  @else {
   display: inline-block;
  }
}
