.ama__dr-finder > .ama__h1 {
  color: $purple;
  font-weight: lighter;
  padding: $gutter 0;
  border-bottom: 1px solid $purple;

  @include breakpoint($bp-small) {
    padding: $gutter * 2 0;
  }
}

.ama__dr-finder.home > .ama__h1,
.ama__dr-finder.search > .ama__h1{
  margin-bottom: 0;
  padding-bottom: 0;
  border: none;
}

.ama__dr-finder .subheading {
  color: $purple;
}

.ama__dr-finder.home .subheading {
  padding-bottom: $gutter;
}

.ama__dr-finder:not(.home) .subheading {
  display: none;
}

.ama__dr-finder.homepage .ama__h1 {
  padding: 0;
  margin: 0;
  border: none;
  color: $purple;
}

// Footer subscription form checkbox style.
.dr-finder-subscription-form .form-item-us-citizen {
  display: flex;
  align-items: flex-start;
  gap: 10px;
}

.dr-finder-subscription-form .form-item-us-citizen input {
  height: auto !important;
}

.dr-finder-subscription-form .form-item-us-citizen label {
  display: flex;
  color: white;
  font-size: 10px;
  text-transform: initial;
}

// Since our Drupal menu is not split into two ULs, lets split with CSS.
.dr-finder-footer-adjustments {
  ul {
    column-count: 2;
    width: 100%;

    a {
      font-weight: 500;
    }
  }
}

.dr-finder-back-search {
  color: $purple;
  text-decoration: none;
}

.ama__dr-finder .canned-search-page {
  margin-bottom: $gutter;
}

.ama__dr-finder .dr-finder-search-load-more {
  padding: 125px 0;
  text-align: center;
}

.ama__dr-finder .pager .ama__button--secondary {
  font-weight: $font-weight-regular;
  padding: 10px 45px;
}
