  width: 0;
  height: 0;
  border-bottom: 20px solid transparent;
  border-left: 20px solid #46166B;
  right: -25px;
}
.slick-next:hover {
  border-left: 20px solid #9073A6;
}

/* Dots */
.slick-dots {
  position: absolute;
  bottom: -25px;
  display: block;
  width: 100%;
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center;
}
.slick-dots li {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  margin: 0 5px;
  padding: 0;
  cursor: pointer;
}
.slick-dots li button {
  font-size: 0;
  line-height: 0;
  display: block;
  width: 20px;
  height: 20px;
  padding: 5px;
  cursor: pointer;
  color: transparent;
  border: 0;
  outline: none;
  background: transparent;
}
.slick-dots li button:before {
  border-radius: 50%;
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  content: "";
  text-align: center;
  background-color: #46166B;
}
.slick-dots li button:hover:before, .slick-dots li button:focus:before {
  background-color: #9073A6;
  outline: none;
}
.slick-dots li.slick-active button:before {
  background-color: #9073A6;
}